// ABOUT

.about {
  &__main {
    overflow: hidden;
    position: relative;
    background-color: $colorBlack;
    min-height: 570px;
    @include rmin(1440) {
      min-height: 810px; }
    @include rmin(1920) {
      min-height: 1080px; }

    img {
      z-index: 0;
      position: absolute;
      bottom: 0;
      max-width: 100%;
      max-height: 100%;
      height: auto;
      left: 0; }

    .container {
      z-index: 1;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      height: 100%;
      padding-top: 30px;
      @include rmin(1440) {
        padding-top: 50px; }
      @include rmin(1920) {
        padding-top: 60px; } }

    &-row {
      float: right;
      width: 50%; }

    &-text {
      padding-top: 35px;
      font-size: 21px;
      @include rmin(1440) {
        padding-top: 48px;
        font-size: 30px;
        line-height: 1.4; }
      @include rmin(1920) {
        padding-top: 78px;
        font-size: 40px; } } }

  &__info {
    overflow: hidden;
    position: relative;
    background-color: $colorBlack;
    background-image: url("../img/ImageInfo_about.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    min-height: 580px;
    @include rmin(1440) {
      min-height: 810px; }
    @include rmin(1920) {
      min-height: 1080px; }

    img {
      z-index: 0;
      position: absolute;
      left: 0;
      bottom: 0; }

    .container {
      z-index: 1;
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      height: 100%; }

    &-slider {
      display: flex;
      flex-direction: column-reverse;
      justify-content: center;
      height: 100%;

      &-item {
        text-align: center;
        font-size: 14px;
        letter-spacing: 1px;
        @include rmin(1440) {
          font-size: 22px; }
        @include rmin(1920) {
          font-size: 24px; }

        p {
          width: 75%;
          margin: {
            left: auto;
            right: auto; } } }

      .slick-dots {
        position: relative;
        padding: {
          left: 0; }
        text-align: center;
        padding-bottom: 40px;
        margin-bottom: 80px;
        @include rmin(1440) {
          padding-bottom: 50px;
          margin-bottom: 100px; }
        @include rmin(1920) {
          padding-bottom: 60px;
          margin-bottom: 120px; }

        &:after {
          content: '';
          position: absolute;
          bottom: 0;
          left: 0;
          right: 0;
          height: 2px;
          margin: {
            left: auto;
            right: auto; }
          background-color: $colorNobel;
          width: 95%;
          @include rmin(1440) {
            width: 90%; }
          @include rmin(1920) {
            width: 85%; } }

        li {
          cursor: pointer;
          position: relative;
          display: inline-block;
          text-align: center;
          transition: all .3s ease-in-out;
          width: 180px;
          text-transform: uppercase;
          @include rmin(1440) {
            width: 200px; }
          @include rmin(1920) {
            width: 240px; }

          &.slick-active {
            &:after {
              content: '';
              z-index: 1;
              position: absolute;
              left: 0;
              width: 100%;
              height: 5px;
              background-color: $colorFuelYellow;
              transition: all .3s ease-in-out;
              bottom: -42px;
              @include rmin(1440) {
                bottom: -52px; }
              @include rmin(1920) {
                bottom: -62px; } }

            div {
              p {
                color: $colorFuelYellow !important;
                transition: color .3s ease-in-out; } } }

          div {
            p {
              transition: color .3s ease-in-out;
              &:first-child {
                font: {
                  weight: bold;
                  size: 30px; }
                @include rmin(1440) {
                  font-size: 50px; }
                @include rmin(1920) {
                  font-size: 80px; } }

              &:last-child {
                color: $colorNobel;
                font-size: 20px;
                @include rmin(1440) {
                  font-size: 30px; }
                @include rmin(1920) {
                  font-size: 40px; } } } } } } } }

  &__member {
    padding-top: 60px;
    background-color: $colorNero;
    @include rmin(1440) {
      padding-top: 90px; }
    @include rmin(1920) {
      padding-top: 120px; }

    &-wrap {
      overflow: hidden;
      position: relative;
      width: 100%;
      margin-bottom: 60px;
      background-color: $colorBlack;
      box-shadow: 0 5px 21px 9px rgba(0, 0, 0, 0.3);
      min-height: 380px;
      height: 380px;
      margin-left: 60px;
      margin-right: 60px;
      width: auto;
      @include rmin(1440) {
        max-width: 1170px;
        height: 540px;
        width: 100%;
        margin: 0 auto 80px; }
      @include rmin(1920) {
        max-width: 100%;
        height: 100%;
        min-height: 720px; }

      img {
        z-index: 0;
        position: absolute;
        bottom: 0;
        left: 0;
        max-width: 100%;
        max-height: 100%;
        @include rmin(1440) {
          left: -10%; }
        @include rmin(1920) {
          left: 0; } } }

    &-row {
      z-index: 1;
      position: relative;
      display: flex;
      flex-direction: column;
      align-items: flex-end;
      justify-content: space-between;
      padding: {
        top: 50px;
        right: 90px;
        bottom: 50px; }
      text-align: right;
      min-height: 380px;
      @include rmin(1440) {
        padding: {
          top: 50px;
          bottom: 75px;
          right: 70px; }
        min-height: 550px; }
      @include rmin(1920) {
        min-height: 720px; } }

    &-col {
      > p {
        &:first-child {
          font-size: 25px;
          @include rmin(1440) {
            font-size: 30px; }
          @include rmin(1920) {
            font-size: 40px; } }
        &:last-child {
          color: $colorNobel;
          font-size: 25px;
          transition: color .3s ease-in-out;
          &:hover {
            color: $colorFuelYellow;
            transition: color .3s ease-in-out; }

          @include rmin(1440) {
            font-size: 30px; }
          @include rmin(1920) {
            font-size: 40px; } } } }


    &-cbtn {
      margin-top: 10px; }

    &-title {
      text-transform: uppercase;
      font: {
        weight: bold; }
      color: $colorFuelYellow;
      text-align: center;
      margin-bottom: 30px;
      font-size: 19px;
      @include rmin(1440) {
        margin-bottom: 50px;
        font-size: 30px; }
      @include rmin(1920) {
        margin-bottom: 60px;
        font-size: 40px; } }

    &-grid {
      display: flex;
      flex-wrap: wrap;

      > div {
        cursor: pointer;
        position: relative;
        width: (100% / 3);
        background-color: $colorBlack;
        text-align: center;

        &:hover {
          .about__member-grid-hidden {
            opacity: 1;
            visibility: visible;
            transition: opacity .25s ease-in-out, visible .25s ease-in-out; } }

        img {
          max-width: 100%; } }

      p {
        &:first-child {
          color: $colorNobel;
          font-size: 20px;
          transition: color .3s ease-in-out;
          &:hover {
            color: $colorFuelYellow;
            transition: color .3s ease-in-out; }
          @include rmin(1440) {
            font-size: 22px; }
          @include rmin(1920) {
            font-size: 24px; } }
        &:nth-child(2),
        &:last-child {
          margin-top: 5px;
          font: {
            weight: bold;
            size: 25px; }
          @include rmin(1440) {
            font-size: 30px; }
          @include rmin(1920) {
            font-size: 40px; } } }


      &-hidden {
        opacity: 0;
        visibility: hidden;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        text-transform: uppercase;
        padding-bottom: 45px;
        background: -moz-linear-gradient(top, rgba(0,0,0,0) 50%, rgba(0,0,0,0.8) 100%);
        background: -webkit-linear-gradient(top, rgba(0,0,0,0) 50%,rgba(0,0,0,0.8) 100%);
        background: linear-gradient(to bottom, rgba(0,0,0,0) 50%,rgba(0,0,0,0.8) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00000000', endColorstr='#cc000000',GradientType=0 );
        text-align: center;
        transition: opacity .25s ease-in-out, visible .25s ease-in-out; } } }


  &__reviews {
    background-color: $colorNero;
    min-height: 560px;
    padding-top: 45px;
    @include rmin(1440) {
      min-height: 780px;
      padding-top: 70px; }
    @include rmin(1920) {
      min-height: 1080px;
      padding-top: 120px; }

    .container {
      max-width: (1300 + (40 * 2)) * 1px;
      @include rmin(1440) {
        max-width: 1170px; }
      @include rmin(1920) {
        max-width: (1300 + (40 * 2)) * 1px; } }

    &-title {
      font: {
        weight: bold; }
      color: $colorFuelYellow;
      text-align: center;
      margin-bottom: 91px;
      font-size: 19px;
      text-transform: uppercase;
      @include rmin(1440) {
        margin-bottom: 131px;
        font-size: 30px; }
      @include rmin(1920) {
        margin-bottom: 181px;
        font-size: 40px; } }

    &-row {
      display: flex !important;
      max-width: 800px;
      margin-left: auto;
      margin-right: auto;
      @include rmin(1440) {
        max-width: 100%; }


      &--left {
        width: 280px;
        margin-right: 60px;
        @include rmin(1440) {
          width: 410px; }
        @include rmin(1920) {
          width: 480px; } }


      &--right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        width: calc(100% - (280px + 60px));
        @include rmin(1440) {
          width: calc(100% - (410px + 60px)); }
        @include rmin(1920) {
          width: calc(100% - (480px + 60px)); }

        > p {
          padding-top: 15px;
          font-size: 25px;
          @include rmin(1440) {
            font-size: 30px; }
          @include rmin(1920) {
            font-size: 40px; } } } }

    &-image {
      overflow: hidden;
      position: relative;
      width: 280px;
      height: 280px;
      @include rmin(1440) {
        width: 410px;
        height: 410px; }
      @include rmin(1920) {
        width: 480px;
        height: 480px; }


      img {
        position: absolute;
        top: 0;
        left: 1px;
        width: 100%;
        max-width: 100%;
        height: 100%; } }

    &-wrap {
      padding-bottom: 15px;

      p {
        color: $colorNobel;
        font-size: 12px;
        @include rmin(1440) {
          font-size: 20px; }
        @include rmin(1920) {
          font-size: 24px; } }

      span {
        display: block;
        font-size: 18px;
        @include rmin(1440) {
          font-size: 28px; }
        @include rmin(1920) {
          font-size: 40px; } } } } }
