.cbutton {
  cursor: pointer;
  position: relative;
  display: table;
  min-height: 30px;
  background-color: transparent;
  border: none;
  padding: 0;
  color: #fff;
  text-transform: uppercase;
  margin: {
    left: auto;
    right: auto; }

  &--icon {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    size: 65px 55px;
    // +rmin(1440)
    //   size: 70px 60px
    @include rmin(1440) {
      size: 120px 105px; } }


  &.homepage__main-cbtn,
  &.news__main-cbtn,
  &.school__main-cbtn,
  &.about__main-cbtn,
  &.about__member-cbtn,
  &.contacts__main-cbtn,
  &.programs__main-cbtn,
  &.programs__container-cbtn,
  &.season__box-cbtn,
  &.season__main-cbtn,
  &.season__concept-cbtn,
  &.season__container-cbtn,
  &.menu__cbtn {
    min-height: 55px;
    @include rmin(1440) {
      min-height: 60px; }
    @include rmin(1920) {
      min-height: 105px; } }

  &.homepage__program-cbtn,
  &.school__main-cbtn,
  &.about__main-cbtn,
  &.contacts__main-cbtn,
  &.programs__block-cbtn,
  &.season__concept-cbtn,
  &.menu__cbtn {
    margin-left: 0; }
  &.about__member-cbtn {
    margin-right: 0; }

  &--hover {
    overflow: hidden;

    &:hover {
      .cbutton__block-bg {
        animation-duration: 0.5s;
        animation-fill-mode: both;

        &--0,
        &--1 {
          opacity: 1; }

        &--0 {
          animation-name: slideInDown; }
        &--1 {
          animation-name: slideInUp; } } } }

  &--active {
    &:hover {
      .cbutton__text {
        text-shadow: 1px 1px 10px rgba(0,0,0,0.5);
        transition: text-shadow 0.3s ease-in-out, top 0.3s ease-in-out; }

      .cbutton__block-bg {
        transition: height 0.3s ease-in-out;

        &--0 {
          height: 0; }
        &--1 {
          height: 100%; } } } }


  &__block {
    position: absolute;
    z-index: 0;
    width: 100%;
    height: 100%;

    &-bg {
      position: absolute;
      left: 0;
      width: 100%;
      background-color: $colorFuelYellow;
      transition: height 0.3s ease-in-out;

      .cbutton--hover & {
        opacity: 0;
        transition: 0.3s; }

      &--0 {
        top: 0;
        height: 57%;
        box-shadow: 0 0 40px 0 rgba(0,0,0,0.55);

        .cbutton--hover & {
          box-shadow: none; } }

      &--1 {
        bottom: 0;
        height: 17%;
        box-shadow: 0 0 40px 0 rgba(0,0,0,0.55);

        .cbutton--hover & {
          box-shadow: none; } } } }

  &__icon {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-45%,-45%) scale(0.5);
    z-index: 1;
    // margin:
    //   top: 27px
    //   left: 27px
    //   right: 26px
    @include rmin(1440) {
      transform: translate(-45%,-45%) scale(1); } }


  &__text {
    line-height: 1.2;
    position: relative;
    z-index: 1;
    padding-top: 7px;
    padding: {
      left: 10px;
      right: 10px; }
    font: {
      weight: bold;
      size: 20px; }
    letter-spacing: 4px;
    transition: text-shadow 0.3s ease-in-out, top 0.3s ease-in-out;
    @include rmin(1440) {
      font-size: 25px;
      padding: {
        left: 20px;
        right: 20px; } }
    @include rmin(1920) {
      font-size: 35px;
      padding: {
        left: 30px;
        right: 30px; } }


    .homepage__main-cbtn &,
    .news__main-cbtn &,
    .school__main-cbtn &,
    .about__main-cbtn &,
    .about__member-cbtn &,
    .contacts__main-cbtn &,
    .programs__main-cbtn &,
    .programs__container-cbtn &,
    .season__box-cbtn &,
    .season__main-cbtn &,
    .season__concept-cbtn &,
    .season__container-cbtn &,
    .menu__cbtn & {
      padding-top: 11px;
      letter-spacing: 5px;
      font-size: 40px;
      @include rmin(1440) {
        font-size: 55px; }
      @include rmin(1920) {
        font-size: 80px; } } } }
