// MENU

.order {
  opacity: 0;
  visibility: hidden;
  z-index: 100;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba($colorBlack, 0.5);
  transition: 0.45s;
  text-transform: uppercase;
  .is-activeOrder & {
    opacity: 1;
    visibility: visible; }

  img {
    opacity: 0;
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    transition: opacity 0.35s ease-in-out;

    .is-activeOrder & {
      opacity: 1;
      transition: opacity 0.35s ease-in-out; } }

  &__block {
    overflow: auto;
    position: absolute;
    top: 0;
    right: -100%;
    height: 100%;
    background-color: $colorNero;
    box-shadow: 0 0 36px 24px rgba(0, 0, 0, 0.5);
    transition: right 0.5s ease-in-out;

    .is-activeOrder & {
      right: 0;
      transition: right 0.45s ease-in-out; }

    width: 350px;
    padding: {
      top: 50px;
      left: 30px;
      right: 30px;
      bottom: 107px; }
    @include rmin(1440) {
      width: 495px;
      padding: {
        top: 63px;
        left: 50px;
        right: 50px;
        bottom: 117px; } }
    @include rmin(1920) {
      width: 660px;
      padding: {
        top: 88px;
        left: 60px;
        right: 60px;
        bottom: 127px; } } }

  &__phone,
  &__email {
    &-title {
      margin-bottom: 5px;
      color: $colorNobel;
      font-size: 12px;
      @include rmin(1440) {
        font-size: 17px; }
      @include rmin(1920) {
        font-size: 24px; } }
    &-number {
      position: relative;
      color: $colorWhite;
      font-weight: bold;
      transition: color 0.3s ease-in-out;

      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 3px;
        bottom: 0;
        left: 0;
        background: $colorFuelYellow;
        visibility: hidden;
        border-radius: 5px;
        transform: scaleX(0);
        transition: .25s linear; }

      &:hover {
        color: $colorFuelYellow;
        transition: color 0.3s ease-in-out;

        &:before,
        &:before {
          visibility: visible;
          transform: scaleX(1); } }

      font-size: 20px;
      @include rmin(1440) {
        font-size: 30px; }
      @include rmin(1920) {
        font-size: 40px; } } }

  &__phone {
    &-wrap {
      margin-bottom: 20px;
      @include rmin(1440) {
        margin-bottom: 30px; }
      @include rmin(1920) {
        margin-bottom: 50px; } } }

  &__email {
    &-wrap {
      margin-bottom: 20px;
      @include rmin(1440) {
        margin-bottom: 60px; }
      @include rmin(1920) {
        margin-bottom: 90px; } } }


  &__phone,
  &__email {
    &-wrap {
      text-align: center; } }

  &__form {
    &-title {
      font: {
        weight: bold; }
      text-align: center;
      color: $colorNobel;
      font-size: 10px;
      margin-bottom: 20px;
      @include rmin(1440) {
        font-size: 18px;
        margin-bottom: 22px; }
      @include rmin(1920) {
        font-size: 24px;
        margin-bottom: 36px; } }

    &-social {
      display: flex;
      justify-content: space-between;
      width: 100%;
      height: 30px;
      margin-bottom: 20px;
      @include rmin(1440) {
        height: 60px;
        margin-bottom: 15px; }
      @include rmin(1920) {
        margin-bottom: 30px; }

      a {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 30px;
        height: 30px;
        border: 3px solid $colorBlack;
        transition: border-color 0.3s ease-in-out;
        @include rmin(1440) {
          width: 45px;
          height: 45px;
          border-width: 4px; }
        @include rmin(1920) {
          width: 60px;
          height: 60px;
          border-width: 5px; }

        &:hover {
          border-color: $colorFuelYellow;
          transition: border-color 0.3s ease-in-out;

          i {
            transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
            &:first-child {
              opacity: 0;
              visibility: hidden; }
            &:last-child {
              opacity: 1;
              visibility: visible; } } }

        i {
          position: absolute;
          transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
          transform: scale(0.5);
          &:last-child {
            opacity: 0;
            visibility: hidden; }

          @include rmin(1440) {
            transform: scale(0.75); }
          @include rmin(1920) {
            transform: scale(1); } } } }

    &-field {
      width: 100%;
      min-height: 40px;
      margin-bottom: 10px;
      @include rmin(1440) {
        margin-bottom: 15px; }
      @include rmin(1920) {
        margin-bottom: 30px; }

      &--email {
        margin-bottom: 10px;
        @include rmin(1440) {
          margin-bottom: 23px; }
        @include rmin(1920) {
          margin-bottom: 36px; } }

      &--date {
        margin-bottom: 40px;
        @include rmin(1440) {
          margin-bottom: 35px; }
        @include rmin(1920) {
          margin-bottom: 60px; } }

      input {
        width: 100%;
        height: 40px;
        padding: {
          left: 15px;
          right: 15px; }
        background-color: transparent;
        text-transform: uppercase;
        border: 3px solid $colorBlack;
        font: {
          weight: bold; }
        text-align: left;
        color: $colorFuelYellow;
        transition: border-color 0.3s ease-in-out;
        &::-webkit-input-placeholder {
          text-align: center; }
        &::-moz-placeholder {
          text-align: center; }
        &:-ms-input-placeholder {
          text-align: center; }
        &:-moz-placeholder {
          text-align: center; }

        @include rmin(1440) {
          height: 50px;
          border-width: 4px; }
        @include rmin(1920) {
          height: 60px;
          border-width: 5px; }

        &:focus {
          border-color: $colorFuelYellow;
          transition: border-color 0.3s ease-in-out; }

        font-size: 14px;
        @include rmin(1440) {
          font-size: 20px; }
        @include rmin(1920) {
          font-size: 24px; } } } } }
