// SOCIAL
// =========================

%fontPropertiesSocial {
  color: $colorNobel;
  font-weight: bold; }

// =========================

.social {
  width: 100%;
  text-align: center;
  letter-spacing: 0;
  text-transform: uppercase;

  &__name {
    @extend %fontPropertiesSocial;
    text-shadow: 0px 0px 26px rgba(0, 0, 0, 0.99);
    font-size: 13px;
    transition: color .3s ease-in-out;
    @include rmin(1440) {
      font-size: 18px; }
    @include rmin(1920) {
      font-size: 24px; }

    &:hover {
      color: $colorFuelYellow;
      transition: color .3s ease-in-out; } }

  &__line {
    @extend %fontPropertiesSocial;

    font-size: 13px;
    @include rmin(1440) {
      font-size: 18px; }
    @include rmin(1920) {
      font-size: 24px; } } }
