* {
  box-sizing: border-box;
  outline: none; }

a {
  color: inherit;
  text-decoration: none;
  &:hover, &:focus {
    text-decoration: none; } }

ul {
  list-style: none;
  li {
    display: inline-block; } }

p {
  margin: 0;
  padding: 0;
  font-weight: normal; }

body, html {
  background: $colorBlack;
  color: $colorWhite;
  font-family: 'CenturyGothic', Arial, sans-serif;
  font-size: 16px;
  letter-spacing: 3px;
  min-width: 1024px;
  max-width: 100%;
  margin: 0;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  @include r(1023) {
    overflow-x: auto; } }

html {
  &.is-activeMenu,
  &.is-activeOrder {
    overflow: hidden; } }

.page {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  &__content {
    // used for sticky footer (flexbox way)
    flex: 1 0 auto;
    display: flex;
    flex-direction: column;
    // header offset ex
    padding-top: 65px;
    @include r(1023) {
      padding-top: 0; }
    @include rmin(1440) {
      padding-top: 90px; }
    @include rmin(1920) {
      padding-top: 120px; } } }

.container {
  max-width: (1560 + (40 * 2)) * 1px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 40px;
  padding-right: 40px; }

@include r($mobile) {
  .container {
    padding-left: 20px;
    padding-right: 20px; } }

// REMOVE FOR PRODUCTION !
.dev-bp-debug {
  background: black;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9999;
  color: white;
  padding: 5px;
  font-size: 12px;
  border-bottom-left-radius: 5px; }

input::-webkit-contacts-auto-fill-button {
  visibility: hidden;
  display: none !important;
  pointer-events: none;
  position: absolute;
  right: 0; }
