// SCHOOL

.school {
  &__wrap {
    &:last-child {
      margin-top: 70px;
      margin-bottom: 30px;
      @include rmin(1440) {
        margin-top: 120px;
        margin-bottom: 100px; } } }

  &__main {
    overflow: hidden;
    position: relative;
    background-color: $colorBlack;
    min-height: calc(100vh - (120px + 40px));
    display: flex;
    align-items: center;
    justify-content: center;
    padding-top: 40px;
    @include rmin(1440) {
      min-height: calc(100vh - (120px + 95px));
      padding-top: 95px; }
    @include rmin(1920) {
      min-height: calc(100vh - (120px + 105px));
      padding-top: 105px; }

    .container {
      z-index: 1;
      position: relative;
      left: 0;
      top: 0;
      right: 0;
      height: 100%;
      max-width: 100%; }

    &-bg {
      z-index: 0;
      position: absolute;
      top: 0;
      right: 0; }

    &-man {
      z-index: 0;
      position: absolute;
      top: 0;
      right: 0;
      max-width: 50%;
      max-height: 100%;
      height: auto;
      display: block;
      @include rmin(1440) {
        max-width: 100%; } } }

  &__row {
    width: 50%;
    padding-left: 50px;
    @include rmin(1440) {
      padding-left: 0; }

    > p {
      padding-left: 30px;
      margin-bottom: 10px;
      font-size: 20px;
      @include rmin(1440) {
        font-size: 35px; }
      @include rmin(1920) {
        font-size: 40px; } }


    &-text {
      p {
        font: {
          weight: normal; }
        display: block;
        font-size: 14px;
        letter-spacing: 1.1px;
        line-height: 1.5;
        @include rmin(1440) {
          font-size: 22px; }
        @include rmin(1920) {
          font-size: 24px; }


        &:first-child {
          margin: {
            top: 40px;
            bottom: 30px; }
          @include rmin(1440) {
            margin: {
              top: 60px;
              bottom: 48px; } }
          @include rmin(1920) {
            margin: {
              top: 70px;
              bottom: 58px; } } }

        &:nth-child(2) {
          margin-bottom: 28px; }

        &:last-child {
          color: $colorFuelYellow;
          margin-bottom: 24px;
          @include rmin(1440) {
            margin-bottom: 74px; }
          @include rmin(1920) {
            margin-bottom: 84px; } } } } }

  .footer__question {
    margin-bottom: 100px;
    @include rmin(1440) {
      margin-bottom: 110px; }
    @include rmin(1920) {
      margin-bottom: 120px; } }

  .social {
    position: relative;
    z-index: 2;
    margin-bottom: 100px; } }
