// CONTACTS

.contacts {
  &__main {
    display: flex;
    align-items: center;
    background-image: url("../img/ImageBg_contacts.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    min-height: calc(100vh - (60px));
    padding-top: 60px;
    @include rmin(1440) {
      padding-top: 100px;
      min-height: calc(100vh - (100px)); }
    @include rmin(1920) {
      padding-top: 120px;
      min-height: calc(100vh - (120px)); }

    &-cbtn {
      margin-bottom: 37px;
      @include rmin(1440) {
        margin-bottom: 77px; }
      @include rmin(1920) {
        margin-bottom: 87px; } }

    .container {
      max-width: 900px;
      padding-bottom: 100px;
      @include rmin(1440) {
        max-width: 1640px; } }

    &-row {
      display: flex;

      &--left,
      &--right {
        width: 50%;
        margin-bottom: 40px;
        @include rmin(1440) {
          margin-bottom: 80px; }
        @include rmin(1920) {
          margin-bottom: 120px; } } } }

  &__phone,
  &__email {
    &-title {
      margin-bottom: 10px;
      font-size: 13px;
      letter-spacing: 1.1px;
      @include rmin(1440) {
        font-size: 22px; }
      @include rmin(1920) {
        font-size: 24px; } }

    &-number {
      position: relative;
      color: $colorFuelYellow;
      font-weight: bold;

      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: 3px;
        bottom: 0;
        left: 0;
        background: $colorFuelYellow;
        visibility: hidden;
        border-radius: 5px;
        transform: scaleX(0);
        transition: .25s linear; }

      &:hover {
        &:before,
        &:before {
          visibility: visible;
          transform: scaleX(1); } }

      font-size: 19px;
      @include rmin(1440) {
        font-size: 30px; }
      @include rmin(1920) {
        font-size: 40px; } } }

  &__phone {
    &-wrap {
      margin-bottom: 25px;
      text-transform: uppercase;
      @include rmin(1440) {
        margin-bottom: 50px; } } }
  &__email {
    &-wrap {
      margin-bottom: 90px;
      text-transform: uppercase;
      @include rmin(1440) {
        margin-bottom: 120px; }
      @include rmin(1920) {
        margin-bottom: 150px; } } }

  &__form {
    float: right;
    width: 100%;
    max-width: 350px;
    min-height: 350px;
    padding: 30px;
    background-color: $colorNero;
    box-shadow: 0 5px 21px 9px rgba(0, 0, 0, 0.3);
    @include rmin(1440) {
      max-width: 660px;
      padding: 60px; }


    &-field {
      width: 100%;
      min-height: 40px;
      margin-bottom: 10px;
      @include rmin(1440) {
        margin-bottom: 25px; }

      &--phone {
        border-color: $colorFuelYellow; }

      &--message {
        min-height: 100px;
        margin-bottom: 30px;
        @include rmin(1440) {
          min-height: 210px;
          margin-bottom: 60px; } }

      input {
        width: 100%;
        height: 40px;
        padding: {
          left: 15px;
          right: 15px; }
        background-color: transparent;
        text-transform: uppercase;
        border: 3px solid $colorBlack;
        font: {
          weight: bold; }
        text-align: left;
        color: $colorFuelYellow;
        transition: border-color 0.3s ease-in-out;
        &::-webkit-input-placeholder {
          text-align: center; }
        &::-moz-placeholder {
          text-align: center; }
        &:-ms-input-placeholder {
          text-align: center; }
        &:-moz-placeholder {
          text-align: center; }

        @include rmin(1440) {
          height: 60px;
          border-width: 5px; }

        &:focus {
          border-color: $colorFuelYellow;
          transition: border-color 0.3s ease-in-out; }

        font-size: 14px;
        @include rmin(1440) {
          font-size: 22px; }
        @include rmin(1920) {
          font-size: 24px; } }

      textarea {
        resize: none;
        width: 100%;
        min-height: 100px;
        text-transform: uppercase;
        padding: 15px;
        background-color: transparent;
        border: 3px solid $colorBlack;
        font: {
          weight: bold; }
        text-align: center;
        color: $colorFuelYellow;
        @include rmin(1440) {
          min-height: 210px;
          border-width: 5px; }

        &:focus {
          border-color: $colorFuelYellow;
          transition: border-color 0.3s ease-in-out; }

        font-size: 14px;
        @include rmin(1440) {
          font-size: 22px; }
        @include rmin(1920) {
          font-size: 24px; } } } } }
