// SOCIAL

// =========================

%fontPropertiesLang {
  color: $colorNobel;
  font-weight: bold; }

// =========================

.lang {
  letter-spacing: 0;
  padding-left: 30px;
  margin: {
    top: 20px;
    bottom: 50px; }
  @include rmin(1440) {
    padding-left: 45px;
    margin: {
      top: 35px;
      bottom: 86px; } }
  @include rmin(1920) {
    padding-left: 55px;
    margin: {
      top: 50px;
      bottom: 101px; } }

  &__name {
    @extend %fontPropertiesLang;
    transition: color .3s ease-in-out;
    text-shadow: 0 0 36px rgba(0, 0, 0, 0.5);
    font-size: 13px;
    @include rmin(1440) {
      font-size: 18px; }
    @include rmin(1920) {
      font-size: 24px; }

    &:hover {
      color: $colorFuelYellow;
      transition: color .3s ease-in-out; } }

  &__line {
    @extend %fontPropertiesLang;

    font-size: 13px;
    @include rmin(1440) {
      font-size: 18px; }
    @include rmin(1920) {
      font-size: 24px; } } }
