// MENU

.menu {
  opacity: 0;
  visibility: hidden;
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  min-height: 100vh;
  background-color: $colorBlack;
  overflow: auto;
  transition: 0.35s;

  .is-activeMenu & {
    opacity: 1;
    visibility: visible; }

  &__img {
    z-index: 0;
    position: absolute;
    max-width: 100%;
    max-height: 100%;
    height: auto;

    &--girl {
      bottom: -5%;
      transform: scale(1.05);
      transform-origin: right bottom;
      right: -5%; }

    &--bg {
      bottom: -5%;
      left: -20%; } }

  .container {
    z-index: 1;
    position: relative;
    max-width: 910px;
    @include rmin(1440) {
      max-width: 1640px; } }

  .row {
    z-index: 0;
    overflow: hidden;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    @include rmin(1440) {
      padding-top: 0;
      min-height: calc(100% - 50px); }
    @include rmin(1920) {
      padding-top: 60px;
      min-height: calc(100% - 60px); } }


  &__cbtn {
    margin-bottom: 5px;
    padding-left: 20px;
    &:last-child {
      margin-bottom: 0; } }


  &__logo {
    cursor: pointer;
    z-index: 1;
    position: relative;
    display: block;
    transition: .45s $ease;
    transform: scale(0.5);
    margin: {
      left: auto;
      right: auto; }
    @include rmin(1440) {
      //margin-top: 50px
      transform: scale(0.75); }
    @include rmin(1920) {
      transform: scale(1);
 } }      //margin-top: 60px

  &__row {
    z-index: 1;
    position: relative;
    text-transform: uppercase;
    padding: {
      top: 0;
      bottom: 77px; }
    @include rmin(1440) {
      padding: {
        top: 25px;
        bottom: 87px; } }
    @include rmin(1920) {
      padding: {
        top: 55px;
        bottom: 97px; } } }

  &__back {
    display: block;
    padding-left: 30px;
    text-transform: uppercase;
    font: {
      weight: bold; }
    color: $colorNobel;
    letter-spacing: 0;
    transition: color 0.3s ease-in-out;
    &:hover {
      color: $colorFuelYellow;
      transition: color 0.3s ease-in-out; }

    font-size: 14px;
    margin-bottom: 31px;
    @include rmin(1440) {
      padding-left: 45px;
      margin: {
        bottom: 10px; }
      font-size: 20px; }
    @include rmin(1920) {
      padding-left: 55px;
      font-size: 24px;
      margin: {
        bottom: 39px; } } } }
