// SEASON

.season {
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: $colorBlack;
    background-image: url("../img/ImageBgMain_season.jpg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    min-height: 510px;
    @include rmin(1440) {
      min-height: 860px; }
    @include rmin(1920) {
      min-height: 960px; }

    &-hash {
      font: {
        weight: bold; }
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 10px;
      font-size: 13px;
      letter-spacing: 1.1px;
      @include rmin(1440) {
        font-size: 22px; }
      @include rmin(1920) {
        font-size: 24px; } } }

  &__concept {
    overflow: hidden;
    position: relative;
    background-color: $colorBlack;
    min-height: 630px;
    @include rmin(1440) {
      min-height: 1100px; }
    @include rmin(1920) {
      min-height: 1200px; }

    .container {
      position: absolute;
      z-index: 1;
      left: 0;
      top: 0;
      right: 0;
      height: 100%;
      padding-top: 60px;
      @include rmin(1440) {
        padding-top: 120px; }

      > p,
      > span {
        font-size: 40px;
        @include rmin(1440) {
          font-size: 50px; }
        @include rmin(1920) {
          font-size: 60px; } }

      > span {
        display: block;
        font-weight: bold;
        margin-bottom: 89px;
        @include rmin(1440) {
          margin-bottom: 169px; }
        @include rmin(1920) {
          margin-bottom: 189px; } } }

    &-text {
      p {
        font-size: 14px;
        line-height: 1.5;
        letter-spacing: 1.1px;
        margin-left: 40%;
        @include rmin(1440) {
          font-size: 22px;
          margin-left: 40%; }
        @include rmin(1920) {
          font-size: 24px;
          margin-left: 50%; } } }

    &-cbtn {
      margin-bottom: 34px;
      @include rmin(1440) {
        margin-bottom: 64px; } }

    &-image {
      position: absolute;

      &--left {
        left: 0;
        bottom: 0; }
      &--right {
        top: 0;
        right: 0;
        display: none;
        @include rmin(1920) {
          display: block; } } } }

  &__box {
    cursor: pointer;
    overflow: hidden;
    position: relative;
    box-shadow: inset 0 0 20px 5px rgba(0, 0, 0, 0.3);
    margin-bottom: 57px;
    margin-left: 50px;
    margin-right: 50px;
    @include rmin(1440) {
      margin-bottom: 100px;
      margin-left: 0;
      margin-right: 0; }
    @include rmin(1920) {
      margin-bottom: 113px; }

    &:hover {
      .season__box-img {
        &:before {
          background-color: rgba($colorBlack, 0.75);
          transition: background-color 0.3s ease-in-out; } }
      .season__box-info {
        opacity: 0;
        visibility: hidden;
        height: 0; }
      .season__box-hover {
        opacity: 1;
        visibility: visible;
        height: 100%; } }

    &-img {
      position: relative;
      width: 100%;
      height: 100%;

      &:before {
        content: "";
        z-index: 1;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($colorBlack, 0.5);
        transition: background-color 0.3s ease-in-out; }

      img {
        z-index: 0;
        position: relative;
        max-width: 100%; } }

    &-info {
      z-index: 1;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;

      p {
        margin-bottom: 10px;
        text-transform: uppercase;
        font: {
          weight: bold; }
        text-align: center;
        font-size: 20px;
        @include rmin(1440) {
          font-size: 22px; }
        @include rmin(1920) {
          font-size: 24px; } } }

    &-hover {
      opacity: 0;
      visibility: hidden;
      z-index: 1;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 0;
      text-align: center;
      transition: 0.5s;

      p,
      span {
        text-shadow: 0 0 36px rgba(0, 0, 0, 0.85); }

      p {
        margin-bottom: 10px;
        font-size: 20px;
        text-transform: uppercase;
        @include rmin(1440) {
          font-size: 35px; }
        @include rmin(1920) {
          font-size: 40px; } }
      span {
        color: $colorFuelYellow;
        font-size: 15px;
        text-transform: uppercase;
        @include rmin(1440) {
          font-size: 22px; }
        @include rmin(1920) {
          font-size: 24px; } } } }


  &__container {
    background-color: $colorNero;
    box-shadow: inset 0 0 20px 5px rgba(0, 0, 0, 0.3);
    padding: {
      top: 50px;
      bottom: 50px; }
    @include rmin(1440) {
      padding: {
        top: 100px;
        bottom: 100px; } }
    @include rmin(1920) {
      padding: {
        top: 112px;
        bottom: 112px; } }

    &-tilte,
    &-subtilte {
      font-size: 30px;
      @include rmin(1440) {
        font-size: 50px; }
      @include rmin(1920) {
        font-size: 60px; } }

    &-tilte {
      margin-bottom: 22px;
      @include rmin(1440) {
        margin-bottom: 26px; }
      @include rmin(1920) {
        margin-bottom: 30px; } }

    &-subtilte {
      color: $colorFuelYellow;
      margin-bottom: 50px;
      @include rmin(1440) {
        margin-bottom: 100px; }
      @include rmin(1920) {
        margin-bottom: 112px; } } }

  &__block {
    position: relative;
    margin-bottom: 61px;
    padding: {
      top: 30px;
      bottom: 30px; }
    @include rmin(1440) {
      margin-bottom: 100px;
      padding: {
        top: 50px;
        bottom: 50px; } }
    @include rmin(1920) {
      margin-bottom: 119px;
      padding: {
        top: 60px;
        bottom: 60px; } }

    &:before,
    &:after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      width: 90%;
      height: 5px;
      margin: auto;
      background-color: $colorFuelYellow; }

    &:before {
      top: 0; }

    &:after {
      bottom: 0; }

    > p {
      width: 80%;
      margin: {
        left: auto;
        right: auto; }
      font: {
        weight: normal; }
      text-align: center;
      font-size: 15px;
      letter-spacing: 1.1px;
      line-height: 1.5;
      @include rmin(1440) {
        font-size: 22px; }
      @include rmin(1920) {
        font-size: 24px; } } }

  &__elem {
    display: flex;
    justify-content: space-between;
    margin-bottom: 100px;
    margin-left: 50px;
    margin-right: 50px;
    @include rmin(1440) {
      margin-bottom: 120px;
      margin-left: 0;
      margin-right: 0; }
    @include rmin(1920) {
      margin-bottom: 140px; }

    > p {
      font-size: 20px;
      @include rmin(1440) {
        font-size: 22px; }
      @include rmin(1920) {
        font-size: 24px; } }

    &-container {
      width: 100%;
      height: 100%;
      max-height: 280px;
      max-width: 280px;
      @include rmin(1440) {
        max-height: 400px;
        max-width: 400px; }
      @include rmin(1920) {
        max-height: 480px;
        max-width: 480px; }

      &:nth-child(2) {
        margin: {
          left: 30px;
          right: 30px; } } }

    &-item {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      margin-bottom: 10px;
      text-transform: uppercase;
      background-color: $colorBlack;
      font: {
        weight: bold; }
      color: $colorFuelYellow;
      text-align: center;
      height: 280px;
      font-size: 25px;
      @include rmin(1440) {
        height: 400px;
        font-size: 30px; }
      @include rmin(1920) {
        height: 480px;
        font-size: 40px; } } } }
