  padding: 15px 30px;
  color: white;
  transition: 0.25s ease-in-out;
  transform-origin: center bottom;
  box-shadow: none;
  will-change: box-shadow, transform;
}

.btn-example:hover {
  transform: scale(1.05) translateY(-5px);
  background: rgba(244, 109, 54, 0.9);
  box-shadow: 0 15px 10px -15px rgba(51, 51, 51, 0.4);
}

.icon {
  display: inline-block;
  font-style: normal;
  content: ' ';
}

.ico {
  fill: currentColor;
  transition: all 0.25s ease-in-out;
}

.ui-group {
  position: relative;
  margin-bottom: 20px;
}

.ui-group label {
  display: block;
  margin-bottom: 10px;
}

.ui-group input, .ui-group textarea {
  display: block;
  width: 100%;
  -webkit-appearance: none;
  background: white;
  color: #999;
  border: 1px solid #e8e8e8;
  border-radius: 4px;
  padding: 12px 15px;
}

.ui-group textarea {
  resize: vertical;
}

.ui-checkbox input {
  display: none;
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
}

.ui-checkbox input:checked + label::before {
  border-color: #4ab68f;
}

.ui-checkbox input:checked + label::after {
  transform: translateY(-50%) scale(1);
}

.ui-checkbox label {
  display: block;
  width: 100%;
  position: relative;
  cursor: pointer;
}

.ui-checkbox label::before {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  display: inline-block;
  content: ' ';
  size: 20px;
  background-color: white;
  border: 1px solid #AFB7C0;
  border-radius: 4px;
  cursor: pointer;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.25s ease-in-out;
}

.ui-checkbox label::after {
  position: absolute;
  left: 6px;
  top: 50%;
  transform: translateY(-50%) scale(0);
  display: inline-block;
  content: ' ';
  size: 10px;
  background: #4ab68f;
  border-radius: 50%;
  transition: transform .2s ease;
}

.ui-checkbox label span {
  display: inline-block;
  padding-left: 35px;
  vertical-align: top;
  line-height: 1.3;
  font-size: 14px;
  cursor: pointer;
}

.mfp-wrap {
  -webkit-overflow-scrolling: touch;
  -webkit-transform: translateZ(0);
}

html.mfp-helper {
  height: 100%;
}

html.mfp-helper body {
  overflow: hidden;
  height: 100%;
  -webkit-transform: translateZ(0);
}

#small-dialog {
  background: white;
  padding: 20px 30px;
  text-align: left;
  max-width: 600px;
  margin: 40px auto;
  position: relative;
}

.popup-buble .popup-dialog {
  opacity: 0;
  transition: all 0.2s ease-in-out;
  transform: scale(0.8);
}

.popup-buble.mfp-ready .popup-dialog {
  opacity: 1;
  transform: scale(1);
}

.popup-buble.mfp-removing .popup-dialog {
  transform: scale(0.8);
  opacity: 0;
}

.popup-buble.mfp-bg {
  background: #4ab68f;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.popup-buble.mfp-ready.mfp-bg {
  opacity: 1;
}

.popup-buble.mfp-removing.mfp-bg {
  opacity: 0;
}

.my-mfp-slide-bottom .zoom-anim-dialog {
  opacity: 0;
  transition: all 0.2s ease-out;
  transform: translateY(-20px) perspective(600px) rotateX(10deg);
}

.my-mfp-slide-bottom.mfp-ready .zoom-anim-dialog {
  opacity: 1;
  transform: translateY(0) perspective(600px) rotateX(0);
}

.my-mfp-slide-bottom.mfp-removing .zoom-anim-dialog {
  opacity: 0;
  transform: translateY(-10px) perspective(600px) rotateX(10deg);
}

.my-mfp-slide-bottom.mfp-bg {
  opacity: 0;
  transition: opacity 0.3s ease-out;
}

.my-mfp-slide-bottom.mfp-ready.mfp-bg {
  opacity: 0.8;
}

.my-mfp-slide-bottom.mfp-removing.mfp-bg {
  opacity: 0;
}

.wow {
  visibility: hidden;
}

.wowFadeUp, .wowFadeDown, .wowFadeLeft, .wowFadeRight {
  opacity: 0;
  animation-fill-mode: forwards;
  animation-timing-function: ease-in;
  animation-duration: 0.3s;
}

.wowFadeUp {
  transform: translate3d(0, 20px, 0);
}

.wowFadeDown {
  transform: translate3d(0, -20px, 0);
}

.wowFadeLeft {
  transform: translate3d(-20px, 0px, 0);
}

.wowFadeRight {
  transform: translate3d(20px, 0px, 0);
}

@keyframes wowFade {
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

