/* HEADER */
.header {
  z-index: 100;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background: $colorBlack;
  transition: 0.5s;
  @include r(1023) {
    position: relative; }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 65px;
    transition: height 0.3s ease-in-out;
    @include rmin(1440) {
      height: 90px;
      // +rmin(1920)
      height: 120px; }

    .is-fixed & {
      height: 55px;
      @include rmin(1440) {
        height: 90px; }
      transition: height 0.3s ease-in-out; } }

  &__hamburger {
    cursor: pointer;
    display: flex;
    align-items: center;
    height: 65px;
    padding-left: 30px;
    @include rmin(1440) {
      height: 90px;
      padding-left: 45px;
      // +rmin(1920)
      height: 120px;
      padding-left: 60px; }
    .is-fixed & {
      height: 55px;
      @include rmin(1440) {
        height: 90px; } }

    &:hover {
      .header__hamburger-line {
        background-color: $colorFuelYellow;
        transition: background-color .25s ease-in-out; }

      .header__hamburger-text {
        color: $colorFuelYellow;
        transition: color .25s ease-in-out; } }

    &-box {
      width: 22px;
      margin-right: 30px;
      @include rmin(1440) {
        width: 30px;
        margin-right: 40px;
        // +rmin(1920)
        width: 42px;
        margin-right: 60px; } }

    &-line {
      display: block;
      width: 100%;
      height: 2px;
      margin-bottom: 2px;
      background-color: $colorWhite;
      transition: background-color .25s ease-in-out;
      @include rmin(1440) {
        height: 3px;
        margin-bottom: 3px;
        // +rmin(1920)
        height: 4px;
        margin-bottom: 3px; }
      &:last-child {
        margin-bottom: 0; }
      &--1,
      &--2 {
        width: 11px;
        margin: {
          left: auto;
          right: auto; }
        @include rmin(1440) {
          width: 15px; }
        @include rmin(1920) {
          width: 21px; } } }

    &-text {
      color: $colorWhite;
      text-transform: uppercase;
      font: {
        weight: bold; }
      transition: color .25s ease-in-out;
      font-size: 11px;
      @include rmin(1440) {
        font-size: 18px;
        // +rmin(1920)
        font-size: 24px; } } }

  &__logotype {
    cursor: pointer;
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    transform: translateY(-50%) scale(0.5);
    margin: auto;
    @include rmin(1440) {
      transform: translateY(-50%) scale(0.75);
      // +rmin(1920)
      transform: translateY(-50%) scale(1); } }


  &__order {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 180px;
    height: 100%;
    padding-right: 35px;
    font-weight: bold;
    color: $colorFuelYellow;
    text-align: right;
    text-transform: uppercase;
    transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
    font-size: 9px;
    @include rmin(1440) {
      width: 300px;
      padding-right: 45px;
      font-size: 14px;
      line-height: 1.5;
      // +rmin(1920)
      width: 310px;
      padding-right: 55px;
      font-size: 24px; }

    &:hover {
      background-color: $colorFuelYellow;
      color: $colorBlack;
      transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out; }
    &:focus {
      background-color: transparent;
      color: $colorFuelYellow; } } }
