// PROGRAMS

.programs {
  &__main {
    display: flex;
    align-items: center;
    background-color: $colorBlack;
    background-image: url("../img/ImageBgMain_programs.jpg");
    background-repeat: no-repeat;
    background-position-x: center;
    background-position-y: 100px;
    background-size: cover;
    background-attachment: fixed;
    min-height: 510px;
    @include rmin(1440) {
      min-height: 860px; }
    @include rmin(1920) {
      min-height: 960px; } }

  &__wrap {
    background-color: $colorNero;
    box-shadow: inset 0 0 20px 5px rgba(0, 0, 0, 0.3);
    padding: {
      top: 50px;
      bottom: 0; }
    @include rmin(1440) {
      padding: {
        top: 80px;
        bottom: 80px; } }
    @include rmin(1920) {
      padding: {
        top: 90px;
        bottom: 90px; } } }

  &__container {
    overflow: hidden;
    position: relative;
    box-shadow: inset 0 0 20px 5px rgba(0, 0, 0, 0.3);
    margin-bottom: 63px;
    margin-left: 50px;
    margin-right: 50px;
    @include rmin(1440) {
      margin-left: 0;
      margin-right: 0;
      margin-bottom: 103px; }
    @include rmin(1920) {
      margin-bottom: 113px; }


    &-video {
      z-index: 0;
      position: relative;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      &:before {
        content: "";
        z-index: 1;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($colorBlack, 0.5); }

      img {
        z-index: 0;
        position: relative;
        max-width: 100%; } }

    &-info {
      z-index: 1;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: center;

      p {
        margin-bottom: 10px;
        font: {
          weight: bold; }
        text-align: center;
        font-size: 20px;
        @include rmin(1440) {
          font-size: 22px; }
        @include rmin(1920) {
          font-size: 24px; } } }

    &-time {
      margin-top: 30px;
      text-align: center;

      i,
      span {
        display: inline-block;
        vertical-align: middle; }
      i {
        margin-right: 30px; }
      span {
        font-size: 25px;
        @include rmin(1440) {
          font-size: 30px; }
        @include rmin(1920) {
          font-size: 40px; } } }

    &-icon {
      display: inline-block;
      vertical-align: middle; }

    &-hover {
      opacity: 0;
      visibility: hidden; } }

  &__block {
    display: flex;
    align-items: center;
    margin-bottom: 50px;
    @include rmin(1440) {
      margin-bottom: 103px; }
    @include rmin(1920) {
      margin-bottom: 113px; }

    &--reverse {
      flex-direction: row-reverse; }

    &--left {
      width: 100%;
      max-width: 470px;
      margin-right: 0;
      padding: {
        left: 50px;
        right: 90px; }
      @include rmin(1440) {
        max-width: 600px;
        margin-right: 120px;
        padding-left: 0;
        padding-right: 0; }
      @include rmin(1920) {
        margin-right: 150px; }

      .programs__block--reverse & {
        margin: {
          right: 0;
          left: 0; }
        padding: {
          left: 100px;
          right: 50px; }
        @include rmin(1440) {
          padding: {
            left: 0;
            right: 0; }
          margin-left: 150px; }
        @include rmin(1920) {
          margin-left: 180px; } }

      > p {
        color: $colorNobel;
        font-size: 11px;
        text-transform: uppercase;
        @include rmin(1440) {
          font-size: 22px; }
        @include rmin(1920) {
          font-size: 24px; }

        .programs__block--reverse & {
          text-align: right; } } }

    &--right {
      width: 100%;
      padding-top: 0;
      padding-bottom: 40px;
      @include rmin(1440) {
        padding-bottom: 50px; }

      .programs__block--reverse & {
        padding-left: 50px;
        @include rmin(1440) {
          padding-left: 0; } }

      > p {
        margin-bottom: 60px;
        font-size: 13px;
        line-height: 1.5;
        letter-spacing: 1.1px;
        @include rmin(1440) {
          margin-bottom: 80px;
          font-size: 22px; }
        @include rmin(1920) {
          font-size: 24px; } } }

    &-video {
      overflow: hidden;
      position: relative;
      width: 100%;
      margin-bottom: 10px;
      box-shadow: 0 5px 21px 9px rgba(0, 0, 0, 0.3);

      img {
        z-index: 0;
        position: relative;
        max-width: 100%;
        max-height: 100%;
        display: block; } }

    &-header {
      overflow: hidden;
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: flex-start;
      margin-bottom: 0;
      @include rmin(1440) {
        margin-bottom: 50px; }
      @include rmin(1920) {
        margin-bottom: 60px; }

      h4 {
        margin: 10px 50px 10px 0;
        text-transform: uppercase;
        font: {
          weight: bold; }
        color: $colorFuelYellow;
        font-size: 20px;
        letter-spacing: 0;
        @include rmin(1440) {
          font-size: 30px; }
        @include rmin(1920) {
          font-size: 40px; } } }

    &-time {
      max-width: 320px;
      white-space: nowrap;

      i {
        margin: {
          right: 15px; }
        transform: scale(0.6);
        @include rmin(1440) {
          margin-right: 25px;
          transform: scale(0.9); }
        @include rmin(1920) {
          transform: scale(1); } }

      span {
        color: $colorNobel;
        font-size: 18px;
        @include rmin(1440) {
          font-size: 35px; }
        @include rmin(1920) {
          font-size: 40px; } } }

    &-icon {
      display: inline-block;
      vertical-align: middle; } }

  &__suit {
    overflow: hidden;

    img {
      display: block;
      max-width: 100%;
      height: auto; }

    &-title {
      margin-bottom: 20px;
      text-transform: uppercase;
      font: {
        weight: bold; }
      color: $colorFuelYellow;
      text-align: center;
      font-size: 20px;
      @include rmin(1440) {
        font-size: 35px; }
      @include rmin(1920) {
        font-size: 40px; } }

    &-item {
      overflow: hidden;
      margin: {
        top: 30px;
        left: 20px;
        right: 20px;
        bottom: 30px; }
      box-shadow: 0 5px 21px 9px rgba(0, 0, 0, 0.3);
      width: 420px;
      @include rmin(1440) {
        width: 450px; }
      @include rmin(1920) {
        width: 480px; } }

    .slick-dots {
      margin-top: 30px;
      padding-left: 0;
      text-align: center;
      display: flex;
      justify-content: center;
      align-items: center;

      li {
        cursor: pointer;
        position: relative;
        width: 30px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        margin: {
          left: 10px;
          right: 10px; }
        transition: width 0.35s ease-in-out, height 0.35s ease-in-out, border 0.35s ease-in-out, background-color 0.35s ease-in-out;
        @include rmin(1440) {
          width: 60px;
          height: 60px; }

        &.slick-active {
          div {
            width: 30px;
            height: 30px;
            background-color: transparent;
            border: 8px solid $colorFuelYellow;
            transition: width 0.35s ease-in-out, height 0.35s ease-in-out, border 0.35s ease-in-out, background-color 0.35s ease-in-out;
            @include rmin(1440) {
              width: 60px;
              height: 60px;
              border-width: 15px; } } }

        div {
          width: 15px;
          height: 15px;
          border-color: transparent;
          background-color: $colorFuelYellow;
          transition: width 0.35s ease-in-out, height 0.35s ease-in-out, border 0.35s ease-in-out, background-color 0.35s ease-in-out;
          @include rmin(1440) {
            width: 30px;
            height: 30px; } } } } } }
